.card {
    position: relative;
}

.card img {
    display: block;
    border: 2px solid #000;
    border-radius: 6px;
}
.card .front {
    transform: rotateY(90deg);
    position: absolute
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

.card .back {
    transition: all ease-in 0.2s
}

.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0;
}